<script>
	export default {
		data() {
			return {
				ishow: false,
				is_lia_show:false,
				screenWidth: document.documentElement.clientHeight,
				content: '',
				xz:[
					{
						title:'',
						titlex:['1','2']
					}
				],
				dsz:0
			}
		},
		props: {
			endTime: {
				type: String,
				default: '1515400000'
			},
			endText: {
				type: String,
				default: '00 : 00 : 00'
			}
		},
		methods: {
			change1(tab){
				if(parseInt(tab) == 1){
					this.is_lia_show = false;
					$('#j-all tr').css('background','rgba(0, 0, 0, 0)');
					this.dsz = 0;
				}else if(parseInt(tab) == 2){
					this.is_lia_show = true;
					$('#j-all tr').css('background','rgba(0, 0, 0, 0)');
					this.dsz = 0;
				}
			},
			change2(tab){
				if(parseInt(tab) == 1){
					this.ishow = false;
				}else if(parseInt(tab) == 2){
					this.ishow = true;
					$('#j-all tr').css('background','rgba(0, 0, 0, 0)');
					this.dsz = 0;
				}
			},
			countdowm(timestamp) {
				let self = this;
				let timer = setInterval(function() {
					let nowTime = new Date();
					let endTime = new Date(timestamp * 1000);
					let t = endTime.getTime() - nowTime.getTime();
					if(t > 0) {
						let day = Math.floor(t / 86400000);
						let hour = Math.floor((t / 3600000) % 24);
						let min = Math.floor((t / 60000) % 60);
						let sec = Math.floor((t / 1000) % 60);
						hour = hour < 10 ? "0" + hour : hour;
						min = min < 10 ? "0" + min : min;
						sec = sec < 10 ? "0" + sec : sec;
						let format = '';
						if(day > 0) {
							format = `${day} : ${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour > 0) {
							format = `${hour} : ${min} : ${sec}`;
						}
						if(day <= 0 && hour <= 0) {
							format = `${min} : ${sec}`;
						}
						self.content = format;
					} else {
						clearInterval(timer);
						self.content = self.endText;
					}
				}, 1000);
			}
		},
		mounted: function() {
			let that = this;
			window.onresize = function() {
				that.screenWidth = document.documentElement.clientHeight;
			}
			$('#j-all tr').click(function(){
				console.log($(this).children());
				if($(this).children()[0].nodeName != "TH" && $(this).children().eq(2).css('display') == 'none'){
					if($(this).css('background-color') == 'rgba(0, 0, 0, 0)'){
					 	 $(this).css('background','rgb(255,245,178)');
					 	that.dsz++
					 }else{
					 	 $(this).css('background','rgba(0, 0, 0, 0)');
					 	that.dsz--
					 }
				}
			})
			// $('#j-all tr').click(function(){
			// 	var tis = [];
			// 	var tis_arr = ['1','2','5']
			// 	 var temp = {id : '123', code : tis_arr};
			// 	 tis.push(temp)
			// 	 console.log(tis)
			// })
			this.countdowm(this.endTime)
		}

	}
</script>
